import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { FileFilterListResponse } from "../../Model/Response/FileFilterListResponse";
import { FileFilterResponse } from "../../Model/Response/FileFilterResponse";
import { FileFilterViewModel } from '../../Model/ViewModel/FileFilterViewModel';
import { FileServicesProvider } from '../file-services/file-services';

/*
  Generated class for the FileFilterServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class FileFilterServicesProvider extends FileServicesProvider {
  private FileFilters: FileFilterListResponse;
  private FileFilter: FileFilterResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    super(Services);
    this.ctl = "FileFilter";
    console.log("Hello FileFilterServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number, keyword: string,exec_group:string,exec_file_seq:number,order_type:boolean) {
    var sub_url: string;
    if(exec_file_seq===0)
    {
      sub_url = "FileFilter?PageSize={1}&PageNumber={2}&keyword={3}&exec_group={4}&order_type={5}";
      sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_group,order_type);
    }
    else
    {
      sub_url = "FileFilter?PageSize={1}&PageNumber={2}&keyword={3}&exec_group={4}&exec_file_seq={5}&order_type={6}";
      sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_group,exec_file_seq,order_type);
    }
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.FileFilters = new FileFilterListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.FileFilters;
    });
  }



  public PostPutAsync(FileFilterViewModel:FileFilterViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "FileFilter";
    else
    sub_url = "FileFilter/{1}";
    sub_url = String.Format(sub_url, this.ctl,FileFilterViewModel.exec_file_seq);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,FileFilterViewModel,mode).map(
      item => {
        this.FileFilter = new FileFilterResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.FileFilter;
      }
    );
  }
}



